import { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import css from '../Table/Table.module.css';

const Table = () => {
  //   const [startYers, setStartYers] = useState(new Date());
  const [startDate, setStartDate] = useState(new Date());
  const [isOpen, setIsOpen] = useState(false);
  const [diagram] = useState([
    {
      name: ' ',
      color: '#FED057',
      value: 1450,
    },
    {
      name: '',
      color: '#FFD8D0',
      value: 3500,
    },
    {
      name: '',
      color: '#FD9498',
      value: 1200,
    },
    {
      name: '  ',
      color: '#C5BAFF',
      value: 900,
    },
    {
      name: '  ',
      color: '#6E78E8',
      value: 750,
    },
    {
      name: '  ',
      color: '#4A56E2',
      value: 200,
    },
    {
      name: '',
      color: '#81E1FF',
      value: 500,
    },
    {
      name: '',
      color: '#24CCA7',
      value: 2000,
    },
    {
      name: ' ',
      color: '#00AD84',
      value: 1500,
    },
  ]);

  let totalDiagram = 0;
  for (let i = 0; i < diagram.length; i += 1) {
    totalDiagram += diagram[i].value;
  }

  const handleChange = e => {
    setIsOpen(!isOpen);
    setStartDate(e);
  };
  const handleClick = e => {
    e.preventDefault();
    setIsOpen(!isOpen);
  };

  return (
    <>
      <div className={css.wraper}>
        <button className={css.table_picker} onClick={handleClick}>
          <span className={css.picker_text}></span>
        </button>
        <button className={css.table_picker} onClick={handleClick}>
          <span className={css.picker_text}></span>
          {isOpen && (
            <DatePicker
              selected={startDate}
              onChange={handleChange}
              showYearPicker
              inline
              dateFormat="yyyy"
            />
          )}
        </button>

        <div className={css.table_head}>
          <p className={css.head_text}></p>
          <p className={css.head_text}></p>
        </div>
        <ul className={css.table_list}>
          {diagram.map(item => {
            return (
              <li className={css.table_item}>
                <span
                  className={css.item_square}
                  style={{ background: item.color }}
                ></span>
                <p className={css.item_text}>{item.name}</p>
                <p className={css.item_number}>{item.value.toFixed(2)}</p>
              </li>
            );
          })}
        </ul>
        <div className={css.table_total}>
          <p className={css.total_text}>:</p>
          <p className={css.total_number}>{totalDiagram.toFixed(2)}</p>
        </div>
        <div className={css.table_total}>
          <p className={css.total_text}>:</p>
          <p className={css.number_income}>5000.00</p>
        </div>
      </div>
    </>
  );
};

export default Table;
